import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "app-skill-levels",
  template: `
    <div class="skill-level">
      <div *ngFor="let i of array" class="circle-progress"
        [class.f]="i < level">
      </div>
    </div>
  `,
  styles: [
    `
      .skill-level {
        display: flex;
        width: 100%;
      }

      .circle-progress {
        border: 1px solid black;
        border-radius: 50%;
        width: 7px;
        height: 7px;
        margin: 0 1px;
      }

      .f {
        background-color: black;
      }
    `,
  ],
})
export class SkillLevelsComponent implements OnInit {
  @Input() level: number
  @Input() maxLevels: number = 5

  array = Array(this.maxLevels).fill(0).map((v,i) => i)

  constructor() {}

  ngOnInit(): void {}
}
