import { Component, OnInit, Input } from '@angular/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-goals-tracking',
  templateUrl: './goals-tracking.component.html',
  styleUrls: ['./goals-tracking.component.scss']
})
export class GoalsTrackingComponent implements OnInit {
  @Input() valueSlider: number = 5
  @Input() goals: {label: string, progress: number}[]

  constructor() { }

  ngOnInit(): void {
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.goals, event.previousIndex, event.currentIndex);
  }

}
