function curve_IEC_inverse(I, DT=0.2) {
  const t = 0.14/(I**0.02 - 1)*DT
  return t
}

function curve_IEC_very_inverse(I, DT=0.2) {
  const t = 13.5/(I - 1)*DT
  return t
}

function curve_IEC_extreme_inverse(I, DT=0.2) {
  const t = 80/(I**2 - 1)*DT
  return t
}

const mapCurves = {
  'curve_IEC_inverse': curve_IEC_inverse,
  'curve_IEC_very_inverse': curve_IEC_very_inverse,
  'curve_IEC_extreme_inverse': curve_IEC_extreme_inverse,
}

function bisection(func: CallableFunction, a: number, b: number, EPSILON=1e-3) {
    if (func(a) * func(b) >= 0) {
        throw Error('f(a)f(b) should be less than 0')
        return;
    }

    let c = a;
    while ((b-a) >= EPSILON)
    {
        // Find middle point
        c = (a+b)/2;

        // Check if middle point is root
        if (func(c) == 0.0)
            break;

        // Decide the side to repeat the steps
        else if (func(c)*func(a) < 0)
            b = c;
        else
            a = c;
    }
    return c
}
