import { Component, OnInit } from "@angular/core";
import * as mathjs from "mathjs";

export interface IGraph {
  data: any[];
  layout: any;
}

@Component({
  selector: "app-newton-univar",
  templateUrl: "./newton-univar.component.html",
  styleUrls: ["./newton-univar.component.scss"],
})
export class NewtonUnivarComponent implements OnInit {
  data = "$b_{2,3}$";

  params = {
    epsilon: 1e-6,
    Re: 15000,
    D: 0.01,
  };

  descriptionText: string = "bla bleu blo";
  stepStates: any[];
  currentStep: number;
  layoutRef: {
    autosize: true;
    title: "Newton method";
    yaxis: { title: "f(x)"; xaxis: { title: "x" } },
    showlegend: false,
  };

  exprDiff = mathjs.derivative(
    "1/sqrt(x) + 2*log10( (epsilon/(3.7*D)) + (2.51/(Re*sqrt(x))))",
    "x"
  );

  public graph: IGraph = {
    data: [{ x: [1, 2, 3], y: [2, 5, 3], type: "scatter" }],
    layout: {
      autosize: true,
      title: "Newton method",
      yaxis: { title: "f(x)", xaxis: { title: "x" } },
      showlegend: false,
    },
  };
  constructor() {}

  calculateCurve() {
    const xValues = mathjs.range(0.001, 0.06, 0.002).toArray();
    const yValues = xValues.map((x) => {
      return calcResCooleBrook(x, this.params);
    });
    const data = { x: xValues, y: yValues, showlegend: false, };

    this.graph.data[0].x = xValues;
    this.graph.data[0].y = yValues;
    return data;
  }

  stepSolution(x0) {
    const y = calcResCooleBrook(x0, this.params);
    this.graph.data.push({
      x: [x0, x0],
      y: [0.0, y],
      type: "scatter",
      line: {
        dash: "dot",
        width: 4,
      },
    });
    return {
      x: [x0, x0],
      y: [0.0, y],
      type: "scatter",
      line: {
        dash: "dot",
        width: 4,
      },
      showlegend: false,
    };
  }

  calcDerivative(x, params) {
    return this.exprDiff.evaluate({ x: x, ...params });
  }

  drawDerivative(x0, params) {
    const y0 = calcResCooleBrook(x0, this.params);
    const diff = this.calcDerivative(x0, params);
    const b = y0 - diff * x0;
    const xf = -b / diff;
    const yf = 0.0;
    this.graph.data.push({
      x: [x0, xf],
      y: [y0, yf],
      type: "scatter",
      line: {
        dash: "dash",
        width: 2,
      },
    });
    return {
      x: [x0, xf],
      y: [y0, yf],
      type: "scatter",
      line: {
        dash: "dash",
        width: 2,
      },
      showlegend: false,
    };
  }

  handleClickStep(stepValue: number) {
    if (stepValue >= 0 && stepValue < this.stepStates.length)
      this.currentStep = stepValue;
  }

  ngOnInit() {
    this.calculateCurve();
    this.stepSolution(0.01);
    console.log(this.calcDerivative(0.01, this.params));
    console.log(this.drawDerivative(0.01, this.params));

    const curve = this.calculateCurve();
    const step1 = this.drawDerivative(0.01, this.params)
    const step2 = this.drawDerivative(step1.x[1], this.params)
    const step3 = this.drawDerivative(step2.x[1], this.params)

    this.stepStates = [
      {
        text:
          "<b>Step 0:</b></br>In this interactive plot you can view the Newton-Raphson steps. Click the next button to plot the curve",
        graph: { data: null, layout: this.layoutRef },
      },
      {
        text: `
          <b>Step 1:</b></br>
          The residual of the function is plotted. The solution is where the plot cross the y=0 line.</br>
          We had to solve the function for many points to get the solution using the function. </br>
          The Newton-Raphson method, as other numerical methods, can provide the solution using few function evaluations. </br>
          Go to the next step to see a interaction of the N-R method.

        `,
        graph: { data: [curve], layout: this.layoutRef },
      },
      {
        text: `
          <b>Step 2:</b></br>
          In the NR- method, a inital guess has to be provided. In this case the guess is 0.01.</br>
          This guess is used to get a first evaluation of the function.
          How can we walk or way to the solution?</br>
          Go next.
        `,
        graph: {
          data: [
            curve,
            this.stepSolution(0.01),
            // this.drawDerivative(0.01, this.params),
          ],
          layout: this.layoutRef,
        },
      },
      {
        text: `
          <b>Step 3:</b></br>
          The N-R method uses the derivative of the function at the current point.</br>
          With the derivative the function is approximated to a line (linear function) and a better approximation for the solution is obtained by this line intersection with the y=0 line.</br>
          As can be seen in the plot.<br>
          Go next.<br>
          $x_{guess}$ = ${0.01}<br>
          $x_{new}$ = ${step1.x[1].toFixed(3)}<br>
          $y_{new}$ = ${step2.y[0].toFixed(3)}
        `,
        graph: {
          data: [
            curve,
            this.stepSolution(0.01),
            this.drawDerivative(0.01, this.params),
          ],
          layout: this.layoutRef,
        },
      },
      {
        text: `
          <b>Step 4:</b></br>
          Now that we have a better approximation of the solution. We can repeat those steps until the solution is sufficient good.</br>
          What is sufficient good?</br>
          Can be the residual value less than a tolerance or the change in the solution less than a tolerance.</br>
          $x_{guess}$ = ${step1.x[1].toFixed(3)}<br>
          $x_{new}$ = ${step2.x[1].toFixed(3)}<br>
          $y_{new}$ = ${step3.y[0].toFixed(3)}
        `,
        graph: {
          data: [
            curve,
            this.stepSolution(step1.x[1]),
            this.drawDerivative(step1.x[1], this.params),
          ],
          layout: this.layoutRef,
        },
      },
    ];
    this.currentStep = 0;
  }
}

// FUNCTIONS
function calcResCooleBrook(x, params) {
  const { epsilon, Re, D } = params;
  const lhs = 1 / mathjs.sqrt(x);
  const rhs =
    -2 * mathjs.log10(epsilon / (3.7 * D) + 2.51 / (Re * mathjs.sqrt(x)));
  return lhs - rhs;
}

function linePointSlope(x, y, slope) {
  const b = y - slope * x;
  return [slope, b];
}
