import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { NewtonUnivarComponent } from './lectures/numerical-methods/newton-univar/newton-univar.component';
import { IecCurveComponent } from './projects/iec-curve/iec-curve.component';
import { IecPageComponent } from './projects/iec-page/iec-page.component';
// import { NewtonUnivarComponent } from './lectures/newton-univar/newton-univar.component';


const routes: Routes = [
  { path: '', component: HomeComponent},
  { path: 'blog', loadChildren: () => import('./blog/blog.module').then(m => m.BlogModule) },
  { path: 'lectures', loadChildren: () => import('./lectures/lectures.module').then(m => m.LecturesModule) },
  { path: 'lectures/newton-univar', component: NewtonUnivarComponent},

  { path: 'projects', loadChildren: () => import('./projects/projects.module').then(m => m.ProjectsModule) },
  { path: 'projects/iec_curves', component: IecPageComponent},

]
;

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
